.social-media-container {
    background-color: #FCFBF4;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-container {
    height: 100%;
    width: 12%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    margin: 0px 5px;
}

img {
    width: 85%;
    height: 85%;
    margin: 0px;
}